@use '../base/variables' as v;

.form {
  .field {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;

    label {
        flex: 0 0 10rem;
    }
    input {
        flex: 1;
        padding: 1rem;
        border: none;
        background-color: lighten(v.$gray, 40%);
    }
  }
}
